<template>
  <div class="container" style="margin-top: 8vh">
    <!-- 轮播图 -->
    <div style="width: 90%; margin-left: 5%; margin-top: 2vh">
      <el-row :gutter="20">
        <el-card>
          <p style="opacity: 0">1</p>
          <el-carousel :interval="4000" type="card" height="200px">
            <el-carousel-item v-for="(item, index) in banners" :key="index">
              <img
                :src="item.imageUrl"
                alt=""
                style="width: 100%; height: 100%"
              />
            </el-carousel-item>
          </el-carousel>
        </el-card>
      </el-row>
    </div>
    <!-- 推荐歌单 -->
    <div style="width: 90%; margin-left: 5%">
      <el-row :gutter="20">
        <el-card shadow="always">
          <h3>推荐歌单</h3>
          <el-col :span="4" v-for="(item, index) in list" :key="index">
            <el-card shadow="hover" style="margin-bottom: 10px; height: 200px">
              <div class="grid-content bg-purple">
                <router-link
                  :to="{ path: '/playlists', query: { id: item.id } }"
                >
                  <div>
                    <img
                      :src="item.picUrl"
                      alt=""
                      :title="item.name"
                      class="Img"
                    />
                  </div>
                  <div
                    style="
                      overflow: hidden;
                      text-overflow: ellipsis;
                      white-space: nowrap;
                      font-size: 12px;
                      color: black;
                    "
                  >
                    {{ item.name }}
                  </div>
                </router-link>
              </div>
            </el-card>
          </el-col>
        </el-card>
      </el-row>
    </div>

    <!-- 最新音乐 -->
    <div style="width: 90%; margin-left: 5%">
      <el-row :gutter="20">
        <el-card shadow="always">
          <h2>最新音乐</h2>
          <el-col :span="4" v-for="(item, index) in songs" :key="index">
            <el-card
              shadow="hover"
              style="margin-bottom: 10px; background-color: "
            >
              <div class="grid-content bg-purple" @click="playmusic(item.id)">
                <img :src="item.picUrl" alt="" />
                <div
                  style="
                    font-size: 14px;
                    color: black;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    font-size: 12px;
                    font-weight: bold;
                  "
                >
                  {{ item.song.artists[0].name }}
                </div>
                <div
                  style="
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    font-size: 12px;
                    color: black;
                  "
                >
                  {{ item.name }}
                </div>
              </div>
            </el-card>
          </el-col>
        </el-card>
      </el-row>
    </div>
    <!-- 推荐MV -->
    <div style="width: 90%; margin-left: 5%">
      <el-row :gutter="20">
        <el-card shadow="always">
          <h2>推荐mv</h2>
          <el-col :span="12" v-for="(item, index) in mvs" :key="index">
            <el-card shadow="hover" style="margin-bottom: 10px; height: 350px">
              <div class="grid-content bg-purple" @click="toMV(item.id)">
                <img :src="item.picUrl" alt="" />
                <div
                  style="
                    font-size: 14px;
                    color: black;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    font-size: 12px;
                    font-weight: bold;
                  "
                >
                  {{ item.artistName }}
                </div>
                <div
                  style="
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    font-size: 12px;
                    color: black;
                  "
                >
                  {{ item.name }}
                </div>
              </div>
            </el-card>
          </el-col>
        </el-card>
      </el-row>
    </div>

    <!-- 底部 -->
    <div style="height: 15vh"></div>
  </div>
</template>

<style>
html {
  background-color: #f1f3f4;
  color: black;
}
a {
  text-decoration: none;
}
.router-link-active {
  text-decoration: none;
}

.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 200px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
.container {
  width: 85%;
  float: right;
  background-color: #f1f3f4;
  color: white;
}

.el-row {
  margin-bottom: 20px;
}
.el-row last-child {
  margin-bottom: 0;
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #ffffff;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.grid-content img {
  width: 100%;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
</style>
<script>
//导入axios\
import axios from "axios";
export default {
  name: "container",
  data() {
    return {
      banners: [],
      //推荐歌单
      list: [],
      songs: [],
      mvs: [],
    };
  },
  created() {
    // console.log(document.cookie);

    //轮播图
    axios({
      method: "get",
      url: "api/banner",
    }).then((res) => {
      // console.log(res);
      this.banners = res.data.banners;
    });
    //推荐歌单
    axios({
      method: "get",
      url: "https://lianghj.top:3000/personalized",
      params: {
        limit: 12,
      },
    }).then((res) => {
      // console.log(res);
      this.list = res.data.result;
    });
    //最新音乐
    axios({
      method: "get",
      url: "https://lianghj.top:3000/personalized/newsong",
      params: {
        limit: 12,
      },
    }).then((res) => {
      // console.log(res);
      // console.log(res.data.result);
      this.songs = res.data.result;
    });

    //mv
    axios({
      method: "get",
      url: "https://lianghj.top:3000/personalized/mv",
    }).then((res) => {
      // console.log(res);
      //  console.log(res.data.result);
      //  console.log(res.data.result[0].picUrl);
      this.mvs = res.data.result;
    });
  },
  methods: {
    toMV(id) {
      this.$router.push(`/mv?q=${id}`);
    },
    // 去歌单详情页
    // toPlaylist(id) {
    //   // 跳转并携带数据即可
    //   console.log(id);
    //   this.$router.push("/playlists");
    // },
    playmusic(id) {
      axios({
        url: "https://lianghj.top:3000/song/url",
        method: "get",
        params: {
          id, // id:id
        },
      }).then((res) => {
        console.log(res);
        let url = res.data.data[0].url;
        // console.log(this.$parent.musicUrl);
        // 设置给父组件的 播放地址
        this.$parent.musicUrl = url;
      });
    },
  },
};
</script>
