<template>
  <div class="container" style="margin-top: 8vh">
    <div class="cou_song">
      <span
        tabindex="1"
        class="item"
        @click="tag = 0"
        :class="{ active: tag == 0 }"
      >
        全部
      </span>
      <span
        tabindex="1"
        class="item"
        @click="tag = 7"
        :class="{ active: tag == 7 }"
      >
        华语
      </span>
      <span
        tabindex="1"
        class="item"
        @click="tag = 96"
        :class="{ active: tag == 96 }"
      >
        欧美
      </span>
      <span
        tabindex="1"
        class="item"
        @click="tag = 8"
        :class="{ active: tag == 8 }"
      >
        日本
      </span>
      <span
        tabindex="1"
        class="item"
        @click="tag = 16"
        :class="{ active: tag == 16 }"
      >
        韩国
      </span>
    </div>
    <el-backtop :bottom="100">
      <div
        style="
           {
            height: 100%;
            width: 100%;
            background-color: #f2f5f6;
            box-shadow: 0 0 6px rgba(0, 0, 0, 0.12);
            text-align: center;
            line-height: 40px;
            color: #1989fa;
          }
        "
      >
        UP
      </div>
    </el-backtop>
    <div style="width: 90%; margin-left: 5%; margin-top: 5vh">
      <el-row :gutter="20">
        <el-card shadow="always">
          <el-col :span="4" v-for="(item, index) in lists" :key="index">
            <el-card
              shadow="hover"
              style="margin-bottom: 10px; background-color: "
            >
              <div class="grid-content bg-purple" @click="playmusic(item.id)">
                <img :src="item.album.picUrl" alt="" />
                <div
                  style="
                    font-size: 14px;
                    color: black;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    font-size: 12px;
                    font-weight: bold;
                  "
                >
                  {{ item.name }}
                </div>
                <div
                  style="
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    font-size: 12px;
                    color: black;
                  "
                >
                  {{ item.album.artists["0"].name }}
                </div>
              </div>
            </el-card>
          </el-col>
        </el-card>
      </el-row>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      lists: [],
      tag: "0",
    };
  },
  watch: {
    tag() {
      this.getList();
    },
  },
  created() {
    this.getList();
  },
  methods: {
    // 获取列表数据
    getList() {
      // 获取 最新音乐数据
      axios({
        url: "https://autumnfish.cn/top/song",
        method: "get",
        params: {
          // 传递 设置的数据
          type: this.tag,
        },
      }).then((res) => {
        // console.log(res)
        this.lists = res.data.data;
        for (let i = 0; i < this.lists.length; i++) {
          let duration = this.lists[i].duration;
          let min = parseInt(duration / 1000 / 60);
          if (min < 10) {
            min = "0" + min;
          }
          let sec = parseInt((duration / 1000) % 60);
          if (sec < 10) {
            sec = "0" + sec;
          }
          this.lists[i].duration = `${min}:${sec}`;
        }
      });
    },
    // 播放歌曲
    playmusic(id) {
      axios({
        method: "get",
        url: "https://lianghj.top:3000/song/url",
        params: {
          id,
        },
      }).then((res) => {
        // console.log(res);
        let url = res.data.data[0].url;
        this.$parent.musicUrl = url;
      });
    },
  },
};
</script>
<style scoped>
.news_two_right {
  width: 75%;
  height: 30px;
  line-height: 30px;
  float: right;
}
.news_two_right span {
  margin-left: 10%;
  display: inline-block;
  font-size: 15px;
  color: rgb(30, 29, 29);
}
.news_two {
  width: 80%;
  margin-left: 10%;
  margin-top: 5vh;
  background-color: #f1f3f4;
}
.news_pic:hover {
  background-color: #f1f3f4;
}
.news_pic {
  width: 45%;
  background-color: #ccc;
  display: inline-block;
  margin: 24px;
}

.content {
  width: 80%;
  margin-left: 10%;
  /* background-color: #fff; */
  color: black;
}
.find_img {
  width: 100px;
}
.cou_song span {
  display: inline-block;
  margin-left: 36px;
}
.cou_song span:focus {
  color: rgb(252, 0, 0);
  font-weight: bold;
}
.cou_song {
  /* position: fixed; */
  width: 100%;
  line-height: 50px;
  /* margin-left: 30%; */
  color: #121212;
  background-color: #f1f3f4;
}
</style>
