  methods: {
    login() {
      this.$router.push("/login");
    },
    GoBack() {
      history.back();
      location.reload();
      // console.log(1);
    },
    toResult() {
      // 非空判断
      if (this.inputValue == "") {
        // 提示用户
        this.$message.warning("请输入内容");
      } else {
        // 去搜索页 携带数据
        this.$router.push("/result?q=" + this.inputValue);
        location.reload();
      }
    },
  },
};
