<template>
  <div class="container">
    <div style="margin-top: 10vh; width: 90%; margin-left: 5%">
      <el-row>
        <el-col :span="6">
          <el-card shadow="always">
            <div>
              <img
                :src="this.playlists.coverImgUrl"
                style="width: 100%; height: 100%"
                alt=""
              />
            </div>
          </el-card>
        </el-col>
        <el-col :span="16" style="margin-left: 20px">
          <el-card shadow="always">
            <div>标题: {{ this.playlists.name }}</div>
            <el-divider></el-divider>
            <div>
              标签<el-divider direction="vertical"></el-divider
              >{{ this.playlists.tags }}
            </div>
            <el-divider></el-divider>

            <div>作者：{{ Nickname }}</div>
            <el-divider></el-divider>
            <div>简介：{{ this.playlists.description }}</div>
          </el-card>
        </el-col>
      </el-row>

      <div
        v-for="(item, index) in tableData"
        :key="index"
        @click="playsongs(item.id)"
        style="line-height: 36px"
      >
        <el-row>
          <el-col :span="6"
            ><div
              class="grid-content bg-purple"
              style="
                width: 280px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
              "
            >
              {{ item.name }}
            </div></el-col
          >
          <el-col :span="6"
            ><div class="grid-content bg-purple">
              {{ item.ar[0].name }}
            </div></el-col
          >
          <el-col :span="6"
            ><div class="grid-content bg-purple">
              {{ item.al.name }}
            </div></el-col
          >
          <el-col :span="6"
            ><div class="grid-content bg-purple" style="text-align: center">
              {{ item.dt }}
            </div></el-col
          >
        </el-row>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      playlists: [],
      Nickname: "",
      tableData: [
        {
          // name: "",
          // album: "",
        },
      ],
    };
  },
  created() {
    axios({
      url: "https://lianghj.top:3000/playlist/detail",
      method: "get",
      params: {
        id: this.$route.query.id,
        // timestamp: Date.now(),
      },
    }).then((res) => {
      // console.log(res);
      // console.log(res.data.playlist);
      // console.log(res.data.playlist.coverImgUrl);
      this.playlists = res.data.playlist;
      console.log(this.playlists);
      console.log(this.playlists.tracks);
      this.coverImgUrl = this.playlists.coverImgUrl;
      this.description = this.playlists.description;
      this.Nickname = this.playlists.creator.nickname;
      this.tags = this.playlists.tags;
      this.name = this.playlists.name;
      // this.id = this.playlists.tracks.id;

      this.tableData = this.playlists.tracks;
      this.tableData.id = this.playlists.tracks.id;
      // this.tableData.album = this.playlists.tracks[0].al.name;
      console.log(this.tableData);
      for (let i = 0; i < this.playlists.tracks.length; i++) {
        let min = parseInt(this.playlists.tracks[i].dt / 1000 / 60);
        let sec = parseInt((this.playlists.tracks[i].dt / 1000) % 60);
        if (min < 10) {
          min = "0" + min;
        }
        if (sec < 10) {
          sec = "0" + sec;
        }
        // console.log(min + "|" + sec);
        this.playlists.tracks[i].dt = min + ":" + sec;
      }
    });
  },
  methods: {
    playsongs(id) {
      axios({
        url: "https://lianghj.top:3000/song/url",
        method: "get",
        params: {
          id, // id:id
        },
      }).then((res) => {
        // console.log(res);
        let url = res.data.data[0].url;
        // console.log(url);
        // alert(url);
        // 设置给父组件的 播放地址
        this.$parent.musicUrl = url;
      });
    },
  },
};
</script>
<style scoped>
* {
  color: black;
}
.grid-content {
  font-size: 14px;
}
</style>
