<template>
  <div class="result-container">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
      <el-form-item label="手机号" prop="phone">
        <el-input v-model="ruleForm.phone" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input
          type="password"
          v-model="ruleForm.password"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="handleLogin">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import axios from "axios";
// import md5 from "js-md5";
export default {
  data() {
    return {
      ruleForm: {
        password: "nie19750116",
        phone: "15938033427",
      },
      rules: {
        phone: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { min: 3, max: 11, message: "长度在3-11之间", trigger: "blur" },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  methods: {
    handleLogin() {
      this.$refs.ruleForm.validate(async (valid) => {
        if (valid) {
          axios
            .post("https://autumnfish.cn/login/cellphone", this.ruleForm)
            .then((res) => {
              console.log(res);
              if (res.status === 200) {
                this.$message({
                  type: "success",
                  message: "登录成功!",
                });
              }
              this.$router.push("/");
            });
        } else {
          console.log("error");
        }
      });
    },
  },
};
</script>
