<template>
  <div class="container" style="margin-top: 8vh">
    <div class="mv_left">
      <h3 class="title">MV详情</h3>
      <!-- mv -->
      <div>
        <video
          controls
          :src="url"
          style="width: 90%; height: 600px"
          autoplay
        ></video>
      </div>
    </div>
  </div>
</template>

<script>
// 导入 axios
import axios from "axios";
export default {
  name: "mv",
  data() {
    return {
      // 总条数
      total: 20,
      // 页码
      page: 1,
      // 页容量
      limit: 10,
      // mv地址
      url: "",
      // 相关mv
      simiMvs: [],
      // mv的信息
      mvInfo: {},
      // 头像
      icon: "",
    };
  },
  created() {
    // 获取mv播放地址
    axios({
      url: "https://lianghj.top:3000/mv/url",
      method: "get",
      params: {
        // 获取url中的 id

        id: this.$route.query.q,
      },
    }).then((res) => {
      // console.log(res)
      this.url = res.data.data.url;
    });

    // 获取 mv的信息
    axios({
      url: "https://lianghj.top:3000/mv/detail",
      method: "get",
      params: {
        mvid: this.$route.query.q,
      },
    }).then((res) => {
      // console.log(res)
      // mv的信息
      this.mvInfo = res.data.data;
      // 获取 歌手信息
      axios({
        url: "https://lianghj.top:3000/artists",
        method: "get",
        params: {
          id: this.mvInfo.artists[0].id,
        },
      }).then((res) => {
        // console.log(res);
        // 歌手的封面信息
        this.icon = res.data.artist.picUrl;
      });
    });

    // 获取评论数据
    axios({
      url: "https://lianghj.top:3000/comment/mv",
      method: "get",
      params: {
        id: this.$route.query.q,
        limit: 10,
        offset: 0,
      },
    }).then((res) => {
      console.log(res);
    });
  },
  methods: {
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
  },
};
</script>

<style>
.title {
  color: black;
}
.container {
  height: 92vh;
}
.mv_left {
  width: 90%;
  margin-left: 5%;
}
</style>
